@import url(https://fonts.googleapis.com/css?family=Raleway);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root :-webkit-any-link {
  text-decoration: none;
}

#root :any-link {
  text-decoration: none;
}

p {
  margin: 10px;
}

:root {
    --main: #7dbfc5;
    --accent1: #badc58;
    --accent2: #22a6b3;
    --accent3: #f0932b;
}
.Content {
  text-align: center;
}

.Fab {
   width: 70px;
   height: 70px;
   background-color: var(--main);
   border-radius: 50%;
   box-shadow: 0 6px 10px 0 #666;
   transition: all 0.1s ease-in-out;

   font-size: 40px;
   color: white;
   text-align: center;
   line-height: 50px;
   border: none;
   margin: 10px;
   margin-top: 15px;
}

.Fab:hover {
   box-shadow: 0 6px 14px 0 #666;
   transform: scale(1.05);
   cursor: pointer;
}
.Header {
    overflow: hidden;
    background-color: var(--main);
    position: fixed;
    top: 0;
    width: 100%;
    padding: 20px;
    z-index: 100;
  }
  
  .Header-icon {
    background-color: transparent;
    border: none;
    color: white;
    display: none;
    cursor: pointer;
  }
  
  .Header-logo {
    float: left;
    padding: 5px 16px;
  }
  
  .Header-link {
    float: left;
    text-align: center;
    display: block;
    padding: 10px 16px;
    color: white;
    font-size: 1.3em;
    text-decoration: none;
    background-image: linear-gradient(white, white);
    background-size: 0 2px, auto;
    background-repeat: no-repeat;
    background-position: center bottom;
    transition: all .2s ease-out;
  }
  
  .active, .Header-link:hover {
    background-size: 80% 2px, auto;
    color: white;
  }
  
  

.footer-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 5px solid #f5ecec;
    padding: 2em 3em;
    height: 60px;
    color: gray;
    background-color: #f5ecec;
}

.links > a {
    margin-left: 1em;
  }
.Screen-header {
    padding: 20px;
    padding-top: 100px;
  }
  
  .Screen-title {
    font-size: 2.5em;
  }
  
  @media screen and (max-width: 680px) {
    .Screen-header {
      padding-top: 100px;
    }
  
    .Screen-title {
      font-size: 1.5em;
    }
  }

