.Card {
  margin: 12px;
  display: inline-block;
  width: 24.9999%;
}

@media only screen and (max-width: 700px) {
  .Card {
    width: 49.99999%;
    margin: 6px;
  }
}

@media only screen and (max-width: 500px) {
  .Card {
    width: 100%;
    margin: 6px 0 6px 0;
  }
}

.Card-element {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.Card-element img {
    width: 100%;
    height: 200px;
}

.complete {
    object-fit: cover;
}

.zoom {
    object-fit: cover;
}

.Card-description {
    padding: 15px;
    text-align: center;
    color: black;
}
.container {
    display: flex;
    justify-content: space-between;
    margin: 1em 0;
    line-height: 2em;
    width: 100%;
    white-space: pre-line;
    font-family: "Consolas", "Monaco", "Courier New", Courier, monospace;
    background-color: #f5ecec;
    color: grey;
    text-align: center;
}

.content {
    padding: 1em;
  }

.copy-icon {
    cursor: pointer;
    opacity: 0.7;

}

copy-icon:hover {
    opacity: 1;
  }
.Elements {
    margin-top: 30px;
  }
  
  .Element {
    margin-left: 20px;
  }
  
  @media only screen and (max-width: 700px) {
    .Element {
      margin-left: 6px;
    }
  }
  
  @media only screen and (max-width: 500px) {
    .Element {
      margin-left: 0;
    }
  }
  
  .search {
    vertical-align: middle;
    font-size: large;
    margin: 8px 10px;
    padding: 12px 12px;
    border: 0;
    outline: 0;
    background: transparent;
    border-bottom: 1px solid #ccc;
    box-sizing: border-box;
    width: wrap;
    transition: all .2s ease-out;
  }
  
  .Refresh {
    position: fixed;
    bottom: 20px;
    right: 20px;
    z-index: 1;
  }
